import React from 'react'
import axios from "axios";
import { useEffect, useState } from "react";
import { useNavigate, useParams, Link } from "react-router-dom";
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

export default function Detail_News() {
  AOS.init(); // Initialize AOS

  const [news, setnews] = useState([]);

  const { newsid } = useParams();

  useEffect(() => {
    getLocation();
  }, []);
  const getLocation = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/news.php/${newsid}/?xCase=0`
      );
      setnews(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <div className='body-header'>
      <section >
        <div className='container' data-aos="fade-up">
          <div className='topPic fs-1 fw-bold text-center'>
             {news.namelocation}
          </div>
        </div>
        <div className='page-header w-100'>
          <div className='container'>
             {` > `} {news.titleName}
          </div>
        </div>
        <div className='container' data-aos="fade-up">
          <div className='text-center detailnewsheader fs-3 fw-bold my-3'>
            {news.titleName}
          </div>
          <div className='d-flex justify-content-center m-3'>
            <img src={`https://thewateraccident.000webhostapp.com/${news.newsimg}`} alt="" style={{ width: 300, height: 300 }} />
          </div>
          <div className='newscontent news-descript mt-4'>
            {news.description}
          </div>

        </div>
      </section>
    </div>
  )
}
