import React, { useEffect, useState } from "react";
import axios from "axios";
import "../App.css";
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

export default function App() {
  AOS.init(); // Initialize AOS
  const [weatherData, setWeatherData] = useState(null);
  const [weatherData3h, setWeatherData3h] = useState(null);

  useEffect(() => {
    fetchWeatherData();
    fetchWeatherData3h();
  }, []);

  const fetchWeatherData = async () => {
    try {
      const response = await axios.get(
        "https://api.openweathermap.org/data/2.5/weather?lat=19.999781735588424&lon=99.69969272078599&appid=53b3056c96db132cf7c48377ad78c750"
      );
      setWeatherData(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  const fetchWeatherData3h = async () => {
    try {
      const response = await axios.get(
        "https://api.openweathermap.org/data/2.5/forecast?lat=19.999781735588424&lon=99.69969272078599&appid=53b3056c96db132cf7c48377ad78c750"
      );
      setWeatherData3h(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <section className="weather-sunny text-white text-center" data-aos="fade-up">
      {weatherData ? (
        <div className="fs-1">
          <div>
            {weatherData.name}, {weatherData.sys.country}
          </div>
          <p>{Math.round(weatherData.main.temp / 10)} C </p>
          <p>Weather: {weatherData.weather[0].description}</p>
        </div>
      ) : (
        <p>Loading weather data...</p>
      )}
      <div className="container">

        {weatherData3h ? (
          <section className="fs-1">
            <div className="row fs-3">
              {/* 3h */}
              <div className="col-lg-3 col-2 ms-auto my-2">
                <div className="card-weater py-2">
                  <div className="fs-3">
                    {weatherData3h.list[0].dt_txt.slice(11, 16)}
                  </div>
                  <div className=" fs-1 my-3">
                    <i class="fa-solid fa-cloud"></i>
                  </div>
                  <div className="fs-4">
                    <i class="fa-regular fa-temperature-full"></i> {Math.round(weatherData3h.list[0].main.temp / 10)} C
                    {/* <i class="fa-regular fa-droplet ms-3"></i> {Math.round(weatherData3h.list[0].rain['3h'] / 3 * 100)} % */}
                  </div>
                </div>
              </div>
              {/* 6h */}
              <div className="col-lg-3 col-2 me-auto my-2">
                <div className="card-weater py-2">
                  <div className="fs-3">
                    {weatherData3h.list[1].dt_txt.slice(11, 16)}
                  </div>
                  <div className=" fs-1 my-3">
                    <i class="fa-solid fa-cloud"></i>
                  </div>
                  <div className="fs-4">
                    <i class="fa-regular fa-temperature-full"></i> {Math.round(weatherData3h.list[1].main.temp / 10)} C
                    {/* <i class="fa-regular fa-droplet ms-3"></i> {Math.round(weatherData3h.list[1].rain['3h'] / 3 * 100)} % */}
                  </div>
                </div>
              </div>
            </div>
          </section>
        ) : (
          <p>Loading weather data...</p>
        )}

      </div>


    </section >

  );
}
