import React from 'react'
import axios from "axios";
import { Link } from 'react-router-dom';
import { useEffect, useState } from "react";
import Masonry, { ResponsiveMasonry } from "react-responsive-masonry"
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

export default function Prevent() {
  AOS.init(); // Initialize AOS

  const [locations, setlocations] = useState([]);

  useEffect(() => {
    getLocation();
  }, []);

  const getLocation = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/location.php?xCase=0`
      );
      setlocations(response.data);
    } catch (error) {
      console.error(error);
    }
  };


  return (
    <div className="body-header">
      <section className='container'>
        <div className='topPic fs-1 fw-bold text-center'>
          
        </div>
        <div className='detailheader fs-6 fw-bold ps-2'>
          
        </div>
        <ResponsiveMasonry
          columnsCountBreakPoints={{ 350: 1, 750: 2, 900: 3 }}
        >
          <Masonry columnsCount={3}>
            {locations.map((location, key) => (
              <img
                key={key}
                src={`https://thewateraccident.000webhostapp.com/${location.img}`}
                class="w-100 shadow-1-strong rounded news-img p-1"
                alt="Boat on Calm Water"
              />
            ))}
            
          </Masonry>
        </ResponsiveMasonry>

      </section>
    </div>
  )
}
