import React, { useEffect, useRef, useState } from "react";
import { GoogleMap, useJsApiLoader, Marker,InfoWindow } from '@react-google-maps/api';
import axios from "axios";

export default function Maps() {
  const mapRef = useRef(null);
  const [userLocation, setUserLocation] = useState(null);

  const [locations, setlocations] = useState([]);

  useEffect(() => {
    getLocation();
  }, []);
  const getLocation = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/location.php?xCase=0`
      );
      setlocations(response.data);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    if (navigator.geolocation) {
      navigator.geolocation.getCurrentPosition(
        (position) => {
          const { latitude, longitude } = position.coords;
          setUserLocation({ lat: latitude, lng: longitude });
        },
        (error) => {
          console.error("Error getting geolocation:", error.message);
        }
      );
    } else {
      console.error("Geolocation is not supported by this browser.");
    }
  }, []);

  useEffect(() => {
    if (userLocation) {
      const map = new window.google.maps.Map(mapRef.current, {
        zoom: 12,
        center: userLocation,
      });
      // user
      const UserIcon = {
        url: "https://scontent.fbkk10-1.fna.fbcdn.net/v/t1.15752-9/370300113_830839928646418_8400957653641650059_n.png?_nc_cat=110&ccb=1-7&_nc_sid=ae9488&_nc_eui2=AeEv6rn3nLqq8yiLU1LqSrJGlMOfwZN0XYGUw5_Bk3RdgVSzYmEJBJtLCatF_wXL1GNN_C1DbuMNZeI3TCemTjUJ&_nc_ohc=S7tyZ_81JksAX9yJUBi&_nc_ht=scontent.fbkk10-1.fna&oh=03_AdRlr_5VD5sSuCzF6rP-tlgmFvq18ZECICGPosCFAkRVNw&oe=65113D9F", // Path to the custom marker image
        scaledSize: new window.google.maps.Size(60, 60), // Custom width and height for the icon
      };
      // Add user's location marker
      new window.google.maps.Marker({
        position: userLocation,
        map,
        title: "Your Location",
        icon: UserIcon,
      });
      // 
      const markerhelpIcon = {
        url: "https://scontent.xx.fbcdn.net/v/t1.15752-9/371243616_615902253991149_7968545852206905870_n.png?stp=dst-png_p403x403&_nc_cat=103&ccb=1-7&_nc_sid=aee45a&_nc_eui2=AeHh_wCcJMfd0Wxim78-JoHoIKnTXlcK4sogqdNeVwriygNudR1xxXZlYO4M8b_oT3N6oSUCNPRIA2caya3rkiMq&_nc_ohc=VMT-2ft-AvgAX9zuQVW&_nc_ad=z-m&_nc_cid=0&_nc_ht=scontent.xx&oh=03_AdSF1M9d3D-df6Z_quDVicrrhj4mHZ_qZcOb91QqEXnjUA&oe=65129653", // Path to the custom marker image
        scaledSize: new window.google.maps.Size(40, 40), // Custom width and height for the icon
      };
      // Add other markers
      locations.forEach((location) => {
        new window.google.maps.Marker({
          position: { lat: parseFloat(location.equipLat), lng: parseFloat(location.equipLng) },
          map,
          // title: location.namelocation,
          icon: markerhelpIcon,
        });
      });

      // 
      const markerIcon = {
        url: "https://scontent.xx.fbcdn.net/v/t1.15752-9/370301505_3218249711653477_5702944953409957089_n.png?stp=dst-png_p403x403&_nc_cat=103&ccb=1-7&_nc_sid=aee45a&_nc_eui2=AeFqWMSI50DHO37jgnMWg74Y8SPbDCBDBLDxI9sMIEMEsOYvLrhlvwfg42kTy9heohj0VyC4p4zcuq2649cWQ0FP&_nc_ohc=YJZDskKuU84AX9HinIf&_nc_ad=z-m&_nc_cid=0&_nc_ht=scontent.xx&oh=03_AdTvejYfhCXPESLocVDGsocKq1v8jUB_K0x6kZldjnI2Yw&oe=65127313", // Path to the custom marker image
        scaledSize: new window.google.maps.Size(50, 50), // Custom width and height for the icon
      };
      // Add other markers
      locations.forEach((location) => {
        const marker = new window.google.maps.Marker({
          position: { lat: parseFloat(location.locationLat), lng: parseFloat(location.locationLng) },
          map,
          title: location.namelocation,
          icon: markerIcon,
        });

        // Add click event to open a link
        marker.addListener("click", () => {
          window.location.href = `/TheWaterAccident/Location/detaillocation/${location.id}`; // Change this to your actual route
        });
        
      });

    }
  }, [userLocation, locations]);

  return (
    <div id="map" style={{ width: "100%", height: "400px" }} ref={mapRef} />
  );
};
