import React from 'react'
import axios from "axios";
import { Link } from 'react-router-dom';
import { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/scrollbar';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, Keyboard, Scrollbar, Navigation } from 'swiper/modules';
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

export default function News() {
  AOS.init(); // Initialize AOS

  const [news, setnews] = useState([]);
  const [typenews, settypenews] = useState([]);

  useEffect(() => {
    getNews();
    getTypeNews();

  }, []);

  const getNews = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/news.php?xCase=0`
      );
      setnews(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  const getTypeNews = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/news.php?xCase=4`
      );
      settypenews(response.data);
    } catch (error) {
      console.error(error);
    }
  };


  return (
    <div className="body-header">
      <section className='container'>
        <div className='topPic fs-1 fw-bold text-center'>
          
        </div>
        <div className='ban-news' data-aos="fade-up">

          <Swiper
            slidesPerView={1}
            spaceBetween={30}
            autoplay={{
              delay: 10000,
              disableOnInteraction: false,
            }}
            keyboard={{
              enabled: true,
            }}
            navigation={true}
            modules={[Autoplay, Keyboard, Scrollbar, Navigation]}
            className="mySwiper"
          >
            {news.map((news, key) => (
              <SwiperSlide>
                <Link to={`detailnews/${news.newsID}`} className='text-dark'>
                  <img src={`https://thewateraccident.000webhostapp.com/${news.newsimg}`} class="d-block w-100 object-fit-cover rounded" alt="..." />
                  <div className='fs-5 fw-bold '>{news.titleName}</div>
                </Link>

              </SwiperSlide>
            ))}
          </Swiper>

          <div className='d-flex my-3' data-aos="fade-up">
            {typenews.map((typenews, key) => (
              <div key={key} className='btn btn-more radius-50 px-3 mx-1 fs-5'>{typenews.typeNewsName}</div>
            ))}
            {/* <div className='btn btn-secondary radius-50 px-3 mx-1'></div>
            <div className='btn btn-secondary radius-50 px-3 mx-1'></div>
            <div className='btn btn-secondary radius-50 px-3 mx-1'></div> */}
          </div>
          <div className='row' data-aos="fade-up">
            {news.map((news, key) => (
              <div className='col-lg-4 mb-3'>
                <Link className='text-dark' to={`detailnews/${news.newsID}`}>
                  <div className="row">
                    <div className="col-5 col-lg-12 flex-shrink-0">
                      <img src="img/002.jpg" className='w-100 news-img' alt="..." />
                    </div>
                    <div className="col-7 col-lg-12 flex-grow-1 text-truncate fs-6 mt-2 ">
                      {news.description}
                    </div>
                  </div>
                </Link>
              </div>
            ))}
          </div>
        </div>
      </section>
    </div>
  )
}
