import React from 'react';
import axios from "axios";
import { Link } from 'react-router-dom';
import { useEffect, useState } from "react";
import Weather from '../APIdata/Weather';
import { Swiper, SwiperSlide } from 'swiper/react';
import 'swiper/css';
import 'swiper/css/scrollbar';
import 'swiper/css/navigation';
import 'swiper/css/pagination';
import { Autoplay, Keyboard, Scrollbar, Navigation } from 'swiper/modules';
import AOS from 'aos';
import 'aos/dist/aos.css'; // Import the CSS

function Home() {
  AOS.init(); // Initialize AOS
  const [locations, setlocations] = useState([]);
  const [news, setnews] = useState([]);

  useEffect(() => {
    getLocation();
    getNews();
  }, []);
  
  const getLocation = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/location.php?xCase=0`
      );
      setlocations(response.data);
    } catch (error) {
      console.error(error);
    }
  };
  const getNews = async () => {
    try {
      const response = await axios.get(
        `https://thewateraccident.000webhostapp.com/news.php?xCase=0`
      );
      setnews(response.data);
    } catch (error) {
      console.error(error);
    }
  };


  return (
    <div className='topBar'>
      {/*  */}
      <section className="w-100 vh-100 d-flex flex-column justify-content-center align-items-center banner">
        <div className="text-white text-uppercase fs-1 fw-bold text-center mx-3">
          
        </div>
      </section>
      {/*  */}
      <section className='container' data-aos="fade-up">
        <div className='topPic fs-1 fw-bold text-center'>
          
        </div>
        <div className='mx-3 my-2 detailnewsheader fs-5 fw-bold'>
          
        </div>
        <div>
          {/* new code slide */}
          <div>
            <Swiper
              slidesPerView={1}
              spaceBetween={30}
              autoplay={{
                delay: 5500,
                disableOnInteraction: false,
              }}
              keyboard={{
                enabled: true,
              }}
              breakpoints={{
                576: {
                  slidesPerView: 2,
                },
                992: {
                  slidesPerView: 3,
                },
              }}
              navigation={true}
              modules={[Autoplay, Keyboard, Scrollbar, Navigation]}
              className="mySwiper"
            >
              {locations.map((location, key) => (
                <SwiperSlide>
                  <Link to={`Location/detaillocation/${location.id}`}>
                    <div className="card card-shadow bg-inverse mx-3 my-5">
                      {/* <div className="card-header text-center text-white fw-bold fs-4 bg-risk-1"> */}
                      <div className={`card-header text-center text-white fw-bold fs-4 bg risk-${location.typeriskID}`}>
                        {location.typeriskName}
                      </div>
                      <img className="card-img-bottom object-fit-cover" src={`https://thewateraccident.000webhostapp.com/${location.img}`} alt="Card image cap" height={250} />
                      <div className="card-img-overlay h-100 d-flex flex-column justify-content-end img-shadow-title">
                        <hr className='text-white border border-2 opacity-100 w-75 mx-auto' />
                        <div className="card-title text-center text-white fs-3">{location.namelocation}</div>
                      </div>
                    </div>
                  </Link>
                </SwiperSlide>
              ))}
            </Swiper>
            <div className='d-flex justify-content-center m-3'>
              <div className='btn btn-more radius px-4'>
                
              </div>
            </div>
          </div>
        </div>
      </section>
      {/*  */}
      <Weather></Weather>
      {/*  */}
      <section className='container' data-aos="fade-up">
        <div className='topPic fs-1 fw-bold text-center'>
          
        </div>
        <div>
          <Swiper
            slidesPerView={1}
            spaceBetween={30}
            autoplay={{
              delay: 5500,
              disableOnInteraction: false,
            }}
            keyboard={{
              enabled: true,
            }}
            breakpoints={{
              576: {
                slidesPerView: 2,
              },
              992: {
                slidesPerView: 3,
              },
            }}
            navigation={true}
            modules={[Autoplay, Keyboard, Scrollbar, Navigation]}
            className="mySwiper"
          >
            {news.map((news, key) => (
              <SwiperSlide>
                <div className="card card-shadow my-4">
                  <img src={`https://thewateraccident.000webhostapp.com/${news.newsimg}`} className="card-img-top object-fit-cover" alt="..." height={250} />
                  <div className="card-body">
                    <span className='opacity-50'><i className="far fa-calendar-alt"> </i> {news.created_at} </span>
                    <h5 className="card-title">{news.titleName}</h5>
                    <p className="card-text text-truncate">{news.description}</p>
                    <a href="#" className="btn btn-primary w-100"></a>
                  </div>
                </div>
              </SwiperSlide>
            ))}
          </Swiper>
          <div className='d-flex justify-content-center m-3'>
            <div className='btn btn-more radius px-4'>
              
            </div>
          </div>
        </div>
      </section>

    </div>
  );
}

export default Home;
