import React from 'react'
import { Link } from 'react-router-dom';

export default function Navbar() {
  return (
    <div>
        <nav className="navbar navbar-expand-lg navbar-dark navbar-shadow fixed-top">
          <div className="container">
            <Link className="navbar-brand fs-4" to="/"><img src="img/logo.png" width={60} height={60} alt="" /></Link>
            <button className="navbar-toggler shadow-none border-0" type="button" data-bs-toggle="offcanvas" data-bs-target="#offcanvasNavbar" aria-controls="offcanvasNavbar">
              <span className="navbar-toggler-icon"></span>
            </button>
            <div className="sidebar offcanvas offcanvas-end" tabindex="-1" id="offcanvasNavbar" aria-labelledby="offcanvasNavbarLabel">
              <div className="offcanvas-header text-white border-bottom">
                <h5 className="offcanvas-title" id="offcanvasNavbarLabel"></h5>
                <button type="button" className="btn-close btn-close-white shadow-none" data-bs-dismiss="offcanvas" aria-label="Close"></button>
              </div>
              <div className="offcanvas-body p-4 p-lg-0 d-flex flex-column flex-lg-row">
                <ul className="navbar-nav justify-content-center align-items-center fs-5 flex-grow-1 pe-3">
                  <li className="nav-item">
                    <Link className="nav-link" to="/"></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Location"></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Location_map"></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="Prevent"></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="News"></Link>
                  </li>
                  <li className="nav-item">
                    <Link className="nav-link" to="SOS"></Link>
                  </li>
                </ul>                
                {/* <div className="d-flex flex-row justify-content-center align-items-center gap-3 text-white">
                 <a href="#login" className="btn btn-more"></a>
                </div> */}
              </div>
            </div>
          </div>
        </nav>
      </div>
  )
}
